:root {
  --base-font: "Noto Sans JP", sans-serif;
  --en-font: "Raleway", sans-serif;
  --font-regular: 400;
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;
}

:root {
  --black: #000;
  --white: #fff;
  --dark-gray: #333;
  --red: #FF0000;
  --blue: #3358D3;
  --purple: #411B99;
  --green: #0CA896;
  --gray: #C9C9C9;
  --light-gray: #BABABA;
  --gradient-main: linear-gradient(to right, #431598, #209BA5);
  --gradient-header: linear-gradient(98deg, #f0ecfa 0%, #eff7ff 33.5%, #f0f6f7 69.46%, #f1edfc 100%);
}

:root {
  --z-index-header: 100;
}

:root {
  --header-height: calc(65/16*1rem);
}
@media screen and (min-width: 768px) {
  :root {
    --header-height: calc(90/16*1rem);
  }
}

body {
  font-family: var(--base-font);
  color: var(--black);
}

body.is-noscroll {
  overflow: hidden;
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  scroll-margin-top: 4.0625rem;
}
@media (min-width: 768px) {
  html {
    scroll-margin-top: 5.625rem;
  }
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

:where(dialog) {
  width: unset;
  max-width: unset;
  height: unset;
  max-height: unset;
  padding: unset;
  color: unset;
  background-color: unset;
  border: unset;
  overflow: unset;
}

summary::-webkit-details-marker {
  display: none;
}

/* フォームリセット */
input,
button,
select,
textarea {
  padding: 0;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
}

