@use "global" as *;

.bg-wrapper {
  position: relative;
}

.bg-wrapper::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url(../images/bg-main.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  z-index: -1;
}